* {
  margin: 0;
  padding: 0;

  box-sizing: border-box;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: currentColor;
}

button {
  border: none;
  background: none;
  color: currentColor;

  cursor: pointer;
}

input {
  outline: none;
  border: none;
}